import { createGlobalStyle } from 'styled-components'

const GlobalStyle = createGlobalStyle`

@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700%7CRoboto:400,700');

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  background-color: white;
}
    :root {
    --green: green;
  }
`
export default GlobalStyle